.order {
    background-color: #222021;
    padding: 60px 0;
    color: #F8F8F8;
}

.order__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 564px;
}

.order__title {
    margin: 0;
    margin-bottom: 8px;
    font-size: 36px;
    text-align: center;
}

.order__descr {
    margin: 0;
    margin-bottom: 15px;
    font-size: 14px;
    font-weight: 300;
    text-align: center;
}

.order__input {
    font-size: 24px;
    font-weight: 300;
    width: 100%;
    margin-bottom: 15px;
    padding: 16px 140px 16px 24px;
    background-color: transparent;
    border: 1px solid #F8F8F8;
    color: #F8F8F8;
}

.order__name {
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
}

.order__text {
    resize: none;
    width: 100%;
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 24px;
    min-height: 320px;
}

.order__submit {
    color: #222021;
    background-color: #FEE400;
    width: 320px;
    padding: 20px 0;
    border-radius: 48px;
    border: 2px solid transparent;
    cursor: pointer;
    transition: all .3s ease-in-out;
}

@media (hover:hover) {
    .order__submit:hover {
        background-color: transparent;
        border: 2px solid #F8F8F8;
        color: #F8F8F8;
    }
}