.home__hero {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-attachment: fixed;
}

.hero__cover {
    height: 100vh;
    background-image: url(../../../assets/img/zdaniia_arhitektura_chb_173754_1920x1080.jpg);
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 60px;
}

.home {
    padding-top: 120px;
}

.hero__title {
    margin: 0;
    text-align: center;
    font-size: 61px;
    color: #222021;
    user-select: none;
    margin-bottom: 10px;
}

.hero__text {
    margin-bottom: 0;
    text-align: center;
    margin-bottom: 20px;
}

.hero__link {
    font-size: 16px;
    color: #222021;
    background-color: #FEE400;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 24px;
    border-radius: 48px;
    user-select: none;
    cursor: pointer;
    margin-bottom: 20px;
    border: 2px solid transparent;
    transition: all .3s ease-in-out;
}

@media (hover:hover) {
    .hero__link:hover {
        background-color: transparent;
        border: 2px solid #222021;
    }
}

.about {
    margin-bottom: 80px;
}

.about__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about__left {
    max-width: 448px;
    padding-left: 120px;
}

.about__right {
    -webkit-box-shadow: 10px 10px 0px 0px rgba(254,228,0,1);
-moz-box-shadow: 10px 10px 0px 0px rgba(254,228,0,1);
box-shadow: 10px 10px 0px 0px rgba(254,228,0,1);
}

.about__title {
    margin: 0;
    margin-bottom: 15px;
    font-size: 36px;
}

.about__text {
    font-size: 18px;
    margin: 0;
    font-weight: 300;
}